.directory {
  width: 200px;
  transition: transform 0.2s ease 0.2s;
}
.directory-list {
  position: fixed;
  top: 112px;
  padding-left: 10px;
  font-size: 14px;
  list-style: none;
  z-index: 10;
  transition: top 0.2s ease 0.2s;
  will-change: top;
  margin: 0;
  max-width: 224px;
}
.directory-list .tree-num {
  line-height: 26px;
  border-left: 2px solid #e8e8e8;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: all 0.2s;
  cursor: pointer;
}
.directory-list .tree-num:hover {
  color: #25b864;
}
.directory-list .directory-item-active {
  border-left: 2px solid #25b864;
  color: #25b864;
  box-sizing: content-box;
  box-sizing: initial;
}
.directory-list p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.directory-visible {
  transform: translateY(0);
}
.directory-affixed {
  top: 48px;
}
